<template>
  <div class="Player">
    <div
      :style="style"
      class="player"
      @mousedown="mousedown"
      @touchstart="touchstart"
    >
      <div class="health">
        <div>{{ clientId }}</div>
        <div>{{ health }}</div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import io from 'socket.io-client';
/* eslint-disable import/no-webpack-loader-syntax */
import Worker from 'worker-loader!./worker';

const worker = new Worker();
let hasCrossed;
worker.addEventListener('message', ({ data: intersections }) => {
  // this и ссылка на него не работают.
  hasCrossed(intersections);
});

const rem = 16;

// Частота обновления сервера.
const frequency = 1000 / 60;

// Для рассчетов радиуса: scale * halfSize.
let size = 0;
let halfSize = 0;

let rafId;
let broadcastId;

// Из за горячей перезагрузки может создавать новые подключения.

export default {
  name: 'Player',

  data() {
    return {
      clientId: -1,
      health: 200,

      position: {
        top: 260,
        left: 200,
      },

      scale: 1.0,
      radius: 0,

      shiftY: 0,
      shiftX: 0,

      isCrossed: false,

      // Флаги для частичной отправки информации.
      // Принцип: когда срабатывает наблюдатель, флаг выставляется в true,
      // а когда информация была отправлена, флаг выставляется в false.
      isHealthUpdated: false,
      isPositionUpdated: false,
      isScaleUpdated: false,
    };
  },

  computed: {
    ...mapState('enemies', ['enemies']),

    style() {
      const { position: { left, top }, scale } = this;
      const color = this.getHealthColor();

      return {
        transform: `translate(${left}px, ${top}px) scale(${scale})`,
        backgroundColor: this.isCrossed ? 'darkred' : color,
      };
    },
  },

  watch: {
    health() {
      this.isHealthUpdated = true;
    },

    position: {
      // todo
      deep: true,
      handler() {
        // console.log('isPositionUpdated', this.isPositionUpdated);
        this.isPositionUpdated = true;
      },
    },

    scale() {
      this.isScaleUpdated = true;
    },
  },

  mounted() {
    ({ hasCrossed } = this);

    this.socket();
    this.initial();
    this.runLoop();

    this.broadcast();
    window.onbeforeunload = () => console.log('onbeforeunload');
  },

  destroy() {
    cancelAnimationFrame(broadcastId);
    cancelAnimationFrame(rafId);

    worker.terminate();
  },

  methods: {
    socket() {
      window.app.connection = io.connect(process.env.VUE_APP_BASE_URL, {
        transports: ['websocket'],
      });

      // Сервер выдает идентификатор.
      window.app.connection.on('connected', ({ clientId, clients }) => {
        console.log('connected', clientId);
        console.log('clients', clients);

        this.$store.dispatch('players/addPlayers', clients);
        window.app.clientId = clientId;
        this.clientId = clientId;
      });

      // Обновления clients прослушивается в Players.vue
    },

    broadcast() {
      broadcastId = setTimeout(this.broadcast, frequency);

      if (this.isHealthUpdated) {
        this.isHealthUpdated = false;
        window.app.connection.emit('update', {
          type: 'client',
          payload: {
            health: this.health,
          },
        });
      }

      if (this.isPositionUpdated) {
        this.isPositionUpdated = false;
        window.app.connection.emit('update', {
          type: 'client',
          payload: {
            position: this.position,
          },
        });
      }

      if (this.isScaleUpdated) {
        this.isScaleUpdated = false;
        window.app.connection.emit('update', {
          type: 'client',
          payload: {
            scale: this.scale,
          },
        });
      }
    },

    initial() {
      const styles = getComputedStyle(this.$root.$el);
      const str = styles.getPropertyValue('--size');

      size = Number(str.replace('rem', '')) * rem;
      halfSize = size / 2;

      this.radius = this.scale * halfSize;
    },

    mousedown(e) {
      document.addEventListener('mousemove', this.mousemove);
      document.addEventListener('mouseup', this.mouseup);

      this.setShift(e);
    },

    mousemove({ clientY, clientX }) {
      const { shiftY, shiftX } = this.getShift();

      const top = clientY - shiftY;
      const left = clientX - shiftX;

      this.position = { top, left };
    },

    mouseup() {
      document.removeEventListener('mousemove', this.mousemove);
      document.removeEventListener('mouseup', this.mouseup);
    },

    touchstart({ touches: [e] }) {
      document.addEventListener('touchmove', this.touchmove);
      document.addEventListener('touchend', this.touchend);

      this.setShift(e);
    },

    touchmove({ touches: [e] }) {
      this.mousemove(e);
    },

    touchend() {
      document.removeEventListener('touchmove', this.touchmove);
      document.removeEventListener('touchend', this.touchend);
    },

    setShift({ target, clientY, clientX }) {
      const { top, left } = target.getBoundingClientRect();

      this.shiftY = clientY - top;
      this.shiftX = clientX - left;
    },

    // Высчитываются координаты верхней-левой точки Игрока с учётом изменённого масштаба.
    getShift() {
      const subtractInitialScaling = this.scale - 1;
      const offsetRadius = halfSize * subtractInitialScaling;

      const shiftY = this.shiftY - offsetRadius;
      const shiftX = this.shiftX - offsetRadius;

      return { shiftY, shiftX };
    },

    runLoop() {
      rafId = requestAnimationFrame(this.runLoop);
      this.getIntersections();
    },

    getIntersections() {
      const { enemies, position: { top: y1, left: x1 }, radius: playerRadius } = this;
      worker.postMessage({ enemies, y1, x1, playerRadius, halfSize });
    },

    // Выполняется по событию postMessage из worker'а.
    hasCrossed(intersections) {
      this.isCrossed = intersections.some(({ isIntersected }) => isIntersected);

      if (this.isCrossed) {
        this.reducedHealth();
      }
    },

    reducedHealth() {
      if (this.health > 0) {
        this.health -= 1;
      } else {
        console.info('Game Over!');
      }
    },

    getHealthColor() {
      let color = '';

      switch (true) {
        case this.health > 150:
          color = 'hsl(120, 80%, 40%)';
          break;

        case this.health > 100:
          color = 'hsl(120, 80%, 30%)';
          break;

        case this.health > 50:
          color = 'hsl(120, 80%, 20%)';
          break;

        case this.health > 0:
          color = 'hsl(120, 80%, 10%)';
          break;

        default:
          color = 'black';
      }

      return color;
    },
  },
};
</script>

<style scoped>
.Player {
  position: absolute;
  width: 100vw;
  height: 100vh;
  z-index: 1;
}

.player {
  position: absolute;
  width: var(--size);
  height: var(--size);
  border-radius: 50%;
  transition: background-color 0.1s;
  will-change: transform;
}

.health {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  font-size: 0.8rem;
  font-weight: bold;
  color: white;
  will-change: contents;
  pointer-events: none;
}
</style>
