<template>
  <div class="Players">
    <div
      v-for="player in players"
      :key="player.tick"
      :style="getStyle(player)"
      class="player"
    >
      <div class="health">
        <div>Враг</div>
        <div>{{ player.health }}</div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import merge from 'lodash.merge';

export default {
  name: 'Players',

  computed: {
    ...mapState('players', ['players']),
  },

  mounted() {
    window.app.connection.on('updates', (updates) => {
      delete updates[window.app.clientId];
      console.log('updates', updates);

      // players - текущие игроки, clients - обновлённые игроки.
      Object.entries(updates).forEach(([id, payload]) => {
        console.log('id, payload', id, payload);
        // Возможно обновлённый игрок.
        const player = this.players[id];
        // this.$store.dispatch('players/addPlayers', clients);

        if (player) {
          this.$set(this.players, id, merge(player, payload));
        } else {
          this.$set(this.players, id, payload);
        }
      });
    });

    window.app.connection.on('disconnected', ({ clientId }) => {
      console.log('disconnected', clientId);
      delete this.players[clientId];
    });
  },

  methods: {
    // При первой заходе сервер не высылает позиций других игроков, потому что там их еще нет.
    getStyle(player) {
      const { health, position, scale, isCrossed } = player;
      const color = this.getHealthColor(health);

      let style;

      if (position) {
        style = {
          transform: `translate(${position.left}px, ${position.top}px) scale(${scale})`,
          backgroundColor: isCrossed ? 'darkred' : color,
        };
      } else {
        style = {
          transform: 'translate(0, 0) scale(1)',
          backgroundColor: isCrossed ? 'darkred' : color,
        };
      }

      return style;
    },

    // todo так себе решение, вписать значение по умолчанию здесь - но не важно!
    getHealthColor(health = 200) {
      let color = '';

      switch (true) {
        case health > 150:
          color = 'hsl(120, 80%, 40%)';
          break;

        case health > 100:
          color = 'hsl(120, 80%, 30%)';
          break;

        case health > 50:
          color = 'hsl(120, 80%, 20%)';
          break;

        case health > 0:
          color = 'hsl(120, 80%, 10%)';
          break;

        default:
          color = 'black';
      }

      return color;
    },
  },
};
</script>

<style scoped>
.Players {
  position: absolute;
  width: 100vw;
  height: 100vh;
  pointer-events: none;
}

.player {
  position: absolute;
  width: var(--size);
  height: var(--size);
  border-radius: 50%;
  transition: background-color 0.1s;
  will-change: transform;
}

.health {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  font-weight: bold;
  color: white;
  will-change: contents;
}
</style>
